import {Avatar, Grid, List, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import {DivvyUSDFormat, DivvyCryptoFormat} from "../utils";
import {useSelector} from "react-redux";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import {BNBIcon} from "../utils";
import LocalAtmIcon from '@mui/icons-material/LocalAtm';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';

export default function DivvyFundStats(props) {

    const {project} = props;
    const {bnbPrice, projectsMap} = useSelector(state => state.divvy);

    const {stats} = projectsMap[project.contract];

    const reflectionsBalance = project.divvyFund === 0 ? stats.divvyBalance - (stats.supply * .01) : stats.divvyBalance - project.divvyFund;

    return <Grid container>
        <Grid item xs={12}>
            <Paper style={{padding: 20, border: '1px solid #eee'}} elevation={0}>
                <Typography variant="h6">Divvy Fund</Typography>
                <Grid container>

                    <Grid item xs={12} md={4}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><AccountBalanceWalletIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyCryptoFormat(stats.divvyBalance)} ${project.symbol}`}
                                    secondary="Balance"/>
                            </ListItem>
                        </List>
                    </Grid>

                    <Grid item xs={12} md={4}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><BNBIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyCryptoFormat(stats.divvyBalance * stats.price)} BNB`}
                                    secondary="Value BNB"/>
                            </ListItem>
                        </List>
                    </Grid>

                    <Grid item xs={12} md={4}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><AttachMoneyIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={DivvyUSDFormat(stats.divvyBalance * (stats.price * bnbPrice))}
                                    secondary="Value USD"/>
                            </ListItem>
                        </List>
                    </Grid>

                    <Grid item xs={12} md={4}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><LocalAtmIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyCryptoFormat(reflectionsBalance)} ${project.symbol}`}
                                    secondary="Reflections"/>
                            </ListItem>
                        </List>
                    </Grid>

                    <Grid item xs={12} md={4}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><BNBIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyCryptoFormat(reflectionsBalance * stats.price)} BNB`}
                                    secondary="Reflections Value BNB"/>
                            </ListItem>
                        </List>
                    </Grid>

                    <Grid item xs={12} md={4}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><AttachMoneyIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyUSDFormat((reflectionsBalance) * (stats.price * bnbPrice))}`}
                                    secondary="Reflections Value USD"/>
                            </ListItem>
                        </List>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>
    </Grid>
}