import {Avatar, Grid, IconButton, Tooltip, Typography} from "@mui/material";
import LanguageIcon from '@mui/icons-material/Language';
import TwitterIcon from "@mui/icons-material/Twitter";
import TelegramIcon from "@mui/icons-material/Telegram";
import ShowChartIcon from "@mui/icons-material/ShowChart";
import FindInPageIcon from '@mui/icons-material/FindInPage';
import SwapHorizIcon from '@mui/icons-material/SwapHoriz';

export default function ProjectHeader(props) {

    const {project, infoJSON} = props;

    return <Grid container spacing={2}>
        <Grid item>
            <Avatar
                src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${project.contract}/logo.png`}
                sx={{width: 48, height: 48}}
            />
        </Grid>
        <Grid item style={{flexGrow: 1}}>
            <Typography variant="h4" style={{
                lineHeight: '48px',
                overflow: 'hidden',
                maxWidth: 250,
                whiteSpace: 'nowrap',
                textOverflow: 'ellipsis'
            }}>{project.name}</Typography>
        </Grid>
        <Grid item style={{flexGrow: 1,display: 'flex', justifyContent:'space-around'}}>
            <Tooltip title="Charts">
                <IconButton
                    href={`https://charts.fegex.com/charts/${project.contract}`}
                    target="_blank"
                    style={{
                        color: project.primary
                    }}
                >
                    <ShowChartIcon/>
                </IconButton>
            </Tooltip>
            <Tooltip title="Trade">
                <IconButton
                    href={`https://fegex.com/trade/bsc/${project.contract}`}
                    target="_blank"
                    style={{
                        color: project.primary
                    }}
                >
                    <SwapHorizIcon/>
                </IconButton>
            </Tooltip>
            {
                infoJSON.website && <Tooltip title="Website">
                    <IconButton
                        href={infoJSON.website}
                        target="_blank"
                        style={{
                            color: project.primary
                        }}
                    >
                        <LanguageIcon/>
                    </IconButton>
                </Tooltip>
            }

            {
                infoJSON.social && infoJSON.social.twitter && <Tooltip title="Twitter">
                    <IconButton
                        href={infoJSON.social.twitter}
                        target="_blank"
                        style={{
                            color: project.primary
                        }}
                    >
                        <TwitterIcon/>
                    </IconButton>
                </Tooltip>
            }
            {
                infoJSON.social && infoJSON.social.telegram && <Tooltip title="Telegram">
                    <IconButton
                        href={infoJSON.social.telegram}
                        target="_blank"
                        style={{
                            color: project.primary
                        }}
                    >
                        <TelegramIcon/>
                    </IconButton>
                </Tooltip>
            }
            {
                infoJSON.explorer && <Tooltip title="BSCScan">
                    <IconButton
                        href={infoJSON.explorer}
                        target="_blank"
                        style={{
                            color: project.primary
                        }}
                    >
                        <FindInPageIcon/>
                    </IconButton>
                </Tooltip>
            }
        </Grid>
    </Grid>
}