import {Avatar, Button, Grid, List, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import {DivvyUSDFormat, DivvyCryptoFormat} from "../utils";
import {useDispatch, useSelector} from "react-redux";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import LockIcon from '@mui/icons-material/Lock';
import LocalAtmIcon from '@mui/icons-material/LocalAtm';
import {useEffect, useState} from "react";
import Web3 from "web3";
import faucet_abi from '../abis/faucet_abi.json';
import {updateFaucetRewards} from "../redux/slices/divvySlice";

export default function ProjectFaucet(props) {

    const {project} = props;
    const {bnbPrice, projectsMap, wallet} = useSelector(state => state.divvy);

    const {faucet, stats} = projectsMap[project.contract];

    const [contract, setContract] = useState(null);

    const dispatch = useDispatch();


    useEffect(() => {
        if (wallet.address && window.provider) {
            const web3 = new Web3(window.provider);
            setContract(new web3.eth.Contract(faucet_abi, faucet.contract));
        }
    }, [wallet.address, window.provider]);

    useEffect(() => {
        if (!contract) return;
        const interval = setInterval(() => {
            contract.methods.availableReflections().call((err, resp) => {
                if (err) {
                    console.log(err);
                    return;
                }
                dispatch(updateFaucetRewards({
                    contract: project.contract,
                    faucet_rewards: resp / Math.pow(10, 9) || 0
                }))
            })
        }, 10000);

        return () => clearInterval(interval);
    }, [contract]);

    const claimFaucet = () => {
        if (contract) contract.methods.GetFaucetRewards().send({from: wallet.address});
    }

    return <Grid container>
        <Grid item xs={12}>
            <Paper style={{padding: 20, border: '1px solid #eee'}} elevation={0}>
                <Typography variant="h6">Divvy Faucet</Typography>
                <Grid container>

                    <Grid item xs={12} md={3}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><LockIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyCryptoFormat(faucet.locked.toFixed(0))} ${project.symbol}`}
                                    secondary="Locked"/>
                            </ListItem>
                        </List>
                    </Grid>

                    <Grid item xs={12} md={3}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><LocalAtmIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyCryptoFormat(faucet.reflections)} ${project.symbol}`}
                                    secondary="Available"/>
                            </ListItem>
                        </List>
                    </Grid>

                    <Grid item xs={12} md={3}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><AttachMoneyIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyUSDFormat(faucet.reflections * stats.price * bnbPrice)}`}
                                    secondary="Value"/>
                            </ListItem>
                        </List>
                    </Grid>

                    <Grid item xs={12} md={3}>
                        <List disablePadding>
                            <ListItem>
                                {
                                    wallet.address ? <Button
                                            variant="contained"
                                            onClick={claimFaucet}
                                            disabled={faucet.reflections <= 0}
                                        >Claim {project.symbol}</Button>
                                        :
                                        <></>
                                }
                            </ListItem>
                        </List>
                    </Grid>

                </Grid>
            </Paper>
        </Grid>
    </Grid>
}