import {Avatar, Grid, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import {useSelector} from "react-redux";
import PercentIcon from '@mui/icons-material/Percent';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import {DivvyCryptoFormat} from "../utils";

export default function ProjectToeknomics(props) {

    const {project} = props;
    const {projectsMap} = useSelector(state => state.divvy);

    const {tokenomics} = projectsMap[project.contract];

    return <Grid container>
        <Grid item xs={12}>
            <Paper style={{padding: 20, border: '1px solid #eee'}} elevation={0}>
                <Typography variant="h6">{project.symbol} Tokenomics</Typography>
                <Grid container>
                    <Grid item xs={12} md={4}>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar sx={{bgcolor: project.primary}}><PercentIcon/></Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={tokenomics.taxReflections + " %"}
                                secondary={`Reflections Tax`}/>
                        </ListItem>
                    </Grid>
                    <Grid item xs={12} md={4}>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar sx={{bgcolor: project.primary}}><PercentIcon/></Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={tokenomics.taxAssetBacking + " %"}
                                secondary={`Asset Backing Tax`}/>
                        </ListItem>
                    </Grid>
                    <Grid item xs={12} md={4}>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar sx={{bgcolor: project.primary}}><PercentIcon/></Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={tokenomics.risingPriceFloor + " %"}
                                secondary={`Smart Rising Price Floor`}/>
                        </ListItem>
                    </Grid>
                    <Grid item xs={12} md={4}>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar sx={{bgcolor: project.primary}}><ArrowUpwardIcon/></Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={DivvyCryptoFormat(tokenomics.maxBuy) + " BNB"}
                                secondary={`Max Buy`}/>
                        </ListItem>
                    </Grid>
                    <Grid item xs={12} md={4}>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar sx={{bgcolor: project.primary}}><ArrowDownwardIcon/></Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={`${DivvyCryptoFormat(tokenomics.maxSell)} ${project.symbol}`}
                                secondary={`Max Sell`}/>
                        </ListItem>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>
    </Grid>
}