import {useEffect, useState} from "react";
import {ArcSeries, FlexibleWidthXYPlot} from "react-vis";

import {
    Grid,
    Link,
    Paper,
    Typography
} from "@mui/material";

import axios from "axios";
import {useSelector} from "react-redux";
import ProjectHeader from "../components/ProjectHeader";
import DivvyFundStats from "../components/DivvyFundStats";
import ProjectBurnSupply from "../components/ProjectBurnSupply";
import ProjectNumbers from "../components/ProjectNumbers";
import ProjectLocker from "../components/ProjectLocker";
import ProjectToeknomics from "../components/ProjectTokenomics";
import PersonalStats from "../components/PersonalStats";
import ProjectFaucet from "../components/ProjectFaucet";

const NumberFormatter = new Intl.NumberFormat('en-US', {
    maximumSignificantDigits: 5
});

export default function ProjectDetails(props) {

    const {project} = props;
    const {projectsMap, wallet} = useSelector(state => state.divvy);

    const [infoJSON, setInfoJSON] = useState({});
    const [loading, setLoading] = useState(true);

    const listingMin = 0.01;//percentage

    const stats = projectsMap[project.contract]?.stats;
    const locker = projectsMap[project.contract]?.locker;
    const faucet = projectsMap[project.contract]?.faucet;

    useEffect(() => {
        axios
            .get(`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${project.contract}/info.json`)
            .then(resp => {
                setInfoJSON(resp.data);
                setLoading(false)
            })
            .catch(err=>console.log(err))

    }, [project]);

    if (loading || !projectsMap[project.contract] || !stats) {
        return <></>
    }

    return (<Grid container spacing={2}>

        <Grid item xs={12}>
            <ProjectHeader project={project} infoJSON={infoJSON}/>
        </Grid>

        <Grid item xs={12}>
            <DivvyFundStats project={project} infoJSON={infoJSON}/>
        </Grid>

        {
            stats.divvyBalance < (stats.supply * listingMin) && !project.bypassMin ?
                <Grid item xs={12} style={{marginTop: 30}}>
                    <Paper style={{paddingTop: 20}} align="center">
                        <Typography>Pending Listing. To be considered for listing on Divvy Dashboard
                            send {NumberFormatter.format(stats.supply * listingMin)} {project.symbol} to the <Link
                                href="https://bscscan.com/address/0xb80267ea7fa368374ee4d4bf10044778232adefe"
                                target="_blank">Divvy Fund</Link></Typography>


                        <Grid item xs={12}>
                            <FlexibleWidthXYPlot
                                xDomain={[-5, 5]}
                                yDomain={[-5, 5]}
                                height={300}
                                style={{
                                    transform: 'translate(-15px, 5px)',
                                }}
                            >
                                <ArcSeries
                                    radiusDomain={[0, 10]}
                                    center={{x: 0, y: 0}}
                                    data={[
                                        {
                                            angle0: 0,
                                            angle: Math.PI * 2,
                                            opacity: 1,
                                            radius: 6.75,
                                            radius0: 6,
                                            color: '#eee',
                                        },
                                        {
                                            angle0: 0,
                                            angle: (360 * (stats.divvyBalance / (stats.supply * listingMin)) * (Math.PI / 180)),
                                            opacity: 1,
                                            radius: 8.5,
                                            radius0: 6,
                                            color: project.primary,
                                        },
                                    ]}
                                    colorType={'literal'}
                                />
                            </FlexibleWidthXYPlot>
                            <Typography
                                style={{
                                    height: 0,
                                    width: '100%',
                                    textAlign: 'center',
                                    top: -190,
                                    position: 'relative',
                                    fontSize: 30,
                                    fontWeight: '700',
                                    color: project.primary
                                }}
                            >
                                {NumberFormatter.format((stats.supply * listingMin) - stats.divvyBalance)}
                                <span
                                    style={{
                                        fontSize: 12,
                                        fontWeight: '400',
                                        display: 'block',
                                    }}
                                >UNTIL LISTED</span>
                            </Typography>
                        </Grid>
                    </Paper>
                </Grid> : <>
                    {
                        wallet && wallet.address && <Grid item xs={12}>
                            <PersonalStats project={project}/>
                        </Grid>
                    }
                    {
                        locker && locker.time && <Grid item xs={12}>
                            <ProjectLocker project={project}/>
                        </Grid>
                    }
                    {
                        faucet && faucet.contract && <Grid item xs={12}>
                            <ProjectFaucet project={project}/>
                        </Grid>
                    }
                    <Grid item xs={12}>
                        <ProjectBurnSupply project={project}/>
                    </Grid>

                    <Grid item xs={12}>
                        <ProjectNumbers project={project} infoJSON={infoJSON}/>
                    </Grid>

                    <Grid item xs={12}>
                        <ProjectToeknomics project={project}/>
                    </Grid>
                </>
        }
    </Grid>)
}