import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'

export const BinanceTickerApi = createApi({
  reducerPath: 'BinanceTickerApi',
  baseQuery: fetchBaseQuery({ baseUrl: 'https://api.binance.com/api/v3/' }),
  endpoints: builder => ({
    getTickersByType: builder.query({
      query: (type) => `ticker/${type}`
    })
  })
})

export const { useGetTickersByTypeQuery } = BinanceTickerApi
