import Web3 from 'web3';

import {makeBatchRequest} from "./promisify";
import abi from '../abis/abi.json';

import store from "../redux/store";
import {updatePersonalBalance} from "../redux/slices/divvySlice";

const web3 = new Web3('https://bsc-dataseed.binance.org/');

const getPersonalStats = (contract, address) => {

    const c = new web3.eth.Contract(abi, contract);

    const calls = [
        c.methods.balanceOf(address).call,
    ];

    makeBatchRequest(web3, calls)
        .then(resp => {
            let [
                balance
            ] = resp;
            balance = balance / Math.pow(10, 9) || 0;
            store.dispatch(updatePersonalBalance({
                contract,
                balance
            }));
        })
        .catch(err => {
            console.warn(contract, address, err)
        });
}

export default getPersonalStats;