import {Avatar, Grid, List, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import {DivvyUSDFormat, DivvyCryptoFormat} from "../utils";
import {useSelector} from "react-redux";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import {BNBIcon} from "../utils";
import LocalAtmIcon from '@mui/icons-material/LocalAtm';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';

export default function PersonalStats(props) {

    const {project} = props;
    const {bnbPrice, wallet, projectsMap} = useSelector(state => state.divvy);

    const {stats} = projectsMap[project.contract];

    const balance = wallet.balances[project.contract];

    return <Grid container>
        <Grid item xs={12}>
            <Paper style={{padding: 20, border: '1px solid #eee'}} elevation={0}>
                <Typography variant="h6">Personal Balance</Typography>
                <Grid container>

                    <Grid item xs={12} md={4}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><AccountBalanceWalletIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyCryptoFormat(balance)} ${project.symbol}`}
                                    secondary="Balance"/>
                            </ListItem>
                        </List>
                    </Grid>

                    <Grid item xs={12} md={4}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><BNBIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyCryptoFormat(balance * stats.price)} BNB`}
                                    secondary="Value BNB"/>
                            </ListItem>
                        </List>
                    </Grid>

                    <Grid item xs={12} md={4}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: '#872eff'}}><AttachMoneyIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyUSDFormat(balance * stats.price * bnbPrice)}`}
                                    secondary="Value USD"/>
                            </ListItem>
                        </List>
                    </Grid>

                </Grid>
            </Paper>
        </Grid>
    </Grid>
}