import {configureStore} from '@reduxjs/toolkit';
import {BinanceTickerApi} from "./services/BinanceTickerApi";
import divvyReducer from "./slices/divvySlice";

export default configureStore({
    reducer: {
        [BinanceTickerApi.reducerPath]: BinanceTickerApi.reducer,
        divvy: divvyReducer
    },
    middleware: getDefaultMiddleware => getDefaultMiddleware().concat(
        BinanceTickerApi.middleware,
    )
})
