import Web3 from 'web3';

import {makeBatchRequest} from "./promisify";
import abi from '../abis/abi.json';
import sd_abi from '../abis/sd_abi.json';
import locker_abi from '../abis/locker_abi.json';
import faucet_abi from '../abis/faucet_abi.json';

import store from "../redux/store";
import {updateProjectsMap} from "../redux/slices/divvySlice";

const web3 = new Web3('https://bsc-dataseed.binance.org/');

// const divvyContract = '0xe9490A983292bD844074Ac3e83c3cb408f578b61';
const fBNBContract = '0x87b1AccE6a1958E522233A737313C086551a5c76';
const deadWallet = '0x000000000000000000000000000000000000dead';
const divvyWallet = '0xb80267eA7fa368374ee4d4Bf10044778232AdEFe';

const fBNBcontract = new web3.eth.Contract(abi, fBNBContract)

const getProjectStats = (project) => {

    const {contract, locker, faucet} = project;

    const c = new web3.eth.Contract(sd_abi, contract);

    const calls = [
        // fbnb - balance of fBNB in project
        fBNBcontract.methods.balanceOf(contract).call,

        // liquidity
        c.methods.getBalance(contract).call,

        // burnt
        c.methods.balanceOf(deadWallet).call,

        // supply
        c.methods.totalSupply().call,

        // backing
        c.methods._totalSupply5().call,

        // backingPerToken
        c.methods.oneTokentoBacking().call,

        // divvyBalance
        c.methods.balanceOf(divvyWallet).call,

        // symbol
        c.methods.symbol().call,

        // decimals
        c.methods.decimals().call,

        // maxBuy
        c.methods.MAX_BUY_RATIO().call,

        // maxSell
        c.methods.MAX_SELL_RATIO().call,

        // RPF
        c.methods.RPF().call,

        // Reflections
        c.methods.tx1().call,

        // Asset Backing
        c.methods.Backing().call,

        // totalSupply1/2/6/7/8/9
        // c.methods._totalSupply1().call,
        // c.methods._totalSupply2().call,
        // c.methods._totalSupply6().call,
        // c.methods._totalSupply7().call,
        // c.methods._totalSupply8().call,
        // c.methods._totalSupply9().call,

    ];

    //NOT SUPPORTED BY R0X TOKEN
    if(project.contract !== '0xa3D522c151aD654b36BDFe7a69D0c405193A22F9') {
        // _poolOwner
        calls.push(c.methods._poolOwner().call);
    }

    if (locker) {
        const l = new web3.eth.Contract(locker_abi, locker);

        //locked supply
        calls.push(l.methods.lockedSupply(contract).call);

        //reflections available
        calls.push(l.methods.availableReflections().call);

        //remain time to unlock
        calls.push(l.methods.reaminingTime().call);
    }

    if (faucet) {
        const f = new web3.eth.Contract(faucet_abi, faucet);

        //locked supply
        calls.push(f.methods.lockedSupply(contract).call);

        //reflections available
        calls.push(f.methods.availableReflections().call);
    }

    makeBatchRequest(web3, calls)
        .then(resp => {
            // console.log(project.symbol, resp);
            let [
                fbnb,
                liquidity,
                burnt,
                supply,
                backing,
                backingPerToken,
                divvyBalance,
                symbol,
                decimals,
                maxBuy,
                maxSell,
                risingPriceFloor,
                taxReflections,
                taxAssetBacking,

                // _totalSupply1,
                // _totalSupply2,
                // _totalSupply6,
                // _totalSupply7,
                // _totalSupply8,
                // _totalSupply9,

                //NOT SUPPORTED BY R0X TOKEN
                owner,

                // DIVVY LOCKER INFO
                locked,
                reflections,
                time,

                // DIVVY FAUCET INFO
                faucet_locked,
                faucet_reflections,
            ] = resp;

            fbnb = fbnb / Math.pow(10, 18) || 0;
            liquidity = liquidity / Math.pow(10, 9) || 0;
            burnt = burnt / Math.pow(10, 9) || 0;
            supply = supply / Math.pow(10, 9) || 0;
            backing = backing / Math.pow(10, 18) || 0;
            backingPerToken = backingPerToken / Math.pow(10, 18) || 0;
            divvyBalance = divvyBalance / Math.pow(10, 9) || 0;

            maxBuy = maxBuy / Math.pow(10, 18) || 0;
            maxSell = maxSell / Math.pow(10, 9) || 0;
            risingPriceFloor = 100 - ((risingPriceFloor / 1000) * 100);
            risingPriceFloor = risingPriceFloor < 0 ? 0 : risingPriceFloor;

            taxReflections = 100-taxReflections;//reflections tax
            taxAssetBacking = (1000-taxAssetBacking)/10

            const circulation = supply - burnt || 0;
            const price = (fbnb - backing) / liquidity || 0;

            locked = locked / Math.pow(10, 9) || 0;
            reflections = reflections / Math.pow(10, 9) || 0;

            faucet_locked = faucet_locked / Math.pow(10, 9) || 0;
            faucet_reflections = faucet_reflections / Math.pow(10, 9) || 0;

            // _totalSupply1 = (_totalSupply1).toLocaleString() || undefined;
            // _totalSupply2 = (_totalSupply2).toLocaleString() || undefined;
            // _totalSupply6 = (_totalSupply6).toLocaleString() || undefined;
            // _totalSupply7 = (_totalSupply7).toLocaleString() || undefined;
            // _totalSupply8 = (_totalSupply8).toLocaleString() || undefined;
            // _totalSupply9 = (_totalSupply9).toLocaleString() || undefined;


            const payload = {
                contract,
                symbol,
                decimals,
                owner,
                // _totalSupply1,
                // _totalSupply2,
                // _totalSupply6,
                // _totalSupply7,
                // _totalSupply8,
                // _totalSupply9,
                tokenomics : {
                    maxBuy,
                    maxSell,
                    risingPriceFloor,
                    taxReflections,
                    taxAssetBacking
                },
                stats: {
                    fbnb,
                    liquidity,
                    burnt,
                    supply,
                    backing,
                    circulation,
                    price,
                    backingPerToken,
                    divvyBalance
                },
                locker: {
                    reflections,
                    locked,
                    time
                },
                faucet: {
                    contract: faucet,
                    locked: faucet_locked,
                    reflections: faucet_reflections
                }
            }

            store.dispatch(updateProjectsMap(payload));

            // if (project.symbol === 'FOOK') console.log(project.symbol,payload)

        })
        .catch(err => {
            console.warn(project.name, err)
        });

}

export default getProjectStats;