import {Avatar, Grid, List, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import ShieldIcon from "@mui/icons-material/Shield";
import CalculateIcon from "@mui/icons-material/Calculate";
import InvertColorsIcon from "@mui/icons-material/InvertColors";
import {useSelector} from "react-redux";
import {DivvyUSDFormat, DivvyCryptoFormat} from "../utils";
import {BNBIcon} from "../utils";

export default function ProjectNumbers(props) {

    const {project} = props;
    const {projectsMap, bnbPrice} = useSelector(state => state.divvy);

    const {stats} = projectsMap[project.contract];


    return <Grid container>
        <Grid item xs={12}>
            <Paper style={{padding: 20, border: '1px solid #eee'}} elevation={0}>
                <Typography variant="h6">{project.symbol} Financials</Typography>
                <Grid container>
                    <Grid item xs={12} md={6}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: project.primary}}><AttachMoneyIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={`${DivvyUSDFormat((stats.fbnb - stats.backing) / stats.liquidity * bnbPrice)}`}
                                    secondary={`USD Price`}/>
                            </ListItem>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: project.primary}}><BNBIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={DivvyCryptoFormat(stats.price) + " BNB"}
                                    secondary={`BNB Price`}/>
                            </ListItem>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: project.primary}}><ShieldIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={DivvyCryptoFormat(stats.backingPerToken) + " fBNB"}
                                    secondary="Backing Per Token"/>
                            </ListItem>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: project.primary}}><CalculateIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={(((stats.fbnb - stats.backing) / stats.liquidity) / stats.backingPerToken).toFixed(2) + "x"}
                                    secondary="Baseline To Market Factor"/>
                            </ListItem>
                        </List>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <List disablePadding>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: project.primary}}><AttachMoneyIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={DivvyUSDFormat(stats.circulation * (stats.fbnb - stats.backing) / stats.liquidity * bnbPrice)}
                                    secondary={`Market Cap`}/>
                            </ListItem>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: project.primary}}><InvertColorsIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={DivvyCryptoFormat(stats.liquidity) + ` ${project.symbol}`}
                                    secondary={`Liquidity ${project.symbol}`}/>
                            </ListItem>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: project.primary}}><InvertColorsIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText primary={DivvyCryptoFormat(stats.fbnb - stats.backing) + " fBNB"}
                                              secondary="Liquidity fBNB"/>
                            </ListItem>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar sx={{bgcolor: project.primary}}><ShieldIcon/></Avatar>
                                </ListItemAvatar>
                                <ListItemText primary={DivvyCryptoFormat(stats.backing) + " fBNB"}
                                              secondary="Total Backing"/>
                            </ListItem>
                        </List>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>
    </Grid>;
}