import {Avatar, List, ListItem, ListItemAvatar, ListItemSecondaryAction, ListItemText, styled} from "@mui/material";
import {
    Link as RouterLink, useLocation,
} from "react-router-dom";
import {useSelector} from "react-redux";
import millify from "millify";
import PendingIcon from '@mui/icons-material/Pending';

const DivvyAvatar = styled(Avatar)({
    backgroundColor: 'transparent'
})

export default function ProjectList(props) {

    const {projects, onClick} = props;
    const {bnbPrice, projectsMap} = useSelector(state => state.divvy);

    const {pathname} = useLocation();

    const list = [...projects];

    list.sort((a, b) => {

        if (!projectsMap[a.contract] || !projectsMap[b.contract]) {
            return null;
        }

        const a2 = projectsMap[a.contract];
        const b2 = projectsMap[b.contract];

        if ((a2.stats.circulation * a2.stats.price) < (b2.stats.circulation * b2.stats.price)) {
            return 1;
        }
        if ((a2.stats.circulation * a2.stats.price) > (b2.stats.circulation * b2.stats.price)) {
            return -1;
        }
        return 0;
    });

    return (
        <List>
            {
                list.map((project, i) => {

                    const stats = projectsMap[project.contract]?.stats;

                    if(!stats)return null;

                    const {price, circulation} = stats;

                    let maxDigits = price ? parseFloat(price * bnbPrice).toFixed(12).match(/(\.0*)/)[0].length + 1 : 2;

                    if (maxDigits > 10) maxDigits = 10;
                    if (price * bnbPrice > 1) maxDigits = 0;

                    const NumberFormatter = new Intl.NumberFormat('en-US', {
                        minimumFractionDigits: 2,
                        maximumFractionDigits: maxDigits + 2,
                        currency: 'USD',
                        currencySign: 'standard',
                        currencyDisplay: 'symbol',
                        style: 'currency'
                    });

                    const pending = (stats.divvyBalance < (stats.supply * .01)) && !project.bypassMin;

                    return <RouterLink
                        key={project.symbol}
                        to={`/${project.symbol}`}
                        style={{textDecoration: 'none', color: 'inherit'}}
                        onClick={onClick}
                    >
                        <ListItem
                            button
                            style={{backgroundColor: pathname === `/${project.symbol}` ? '#eee' : ''}}
                        >
                            <ListItemAvatar>
                                <DivvyAvatar>
                                    {
                                        pending ?
                                            <PendingIcon color="primary"/>
                                            :
                                            <img
                                                alt={`${project.name} Logo`}
                                                src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${project.contract}/logo.png`}
                                                width={40}/>
                                    }
                                </DivvyAvatar>
                            </ListItemAvatar>
                            <ListItemText primary={project.symbol} secondary={
                                `${NumberFormatter.format((price * bnbPrice) || 0)} - $${millify((price * bnbPrice * circulation) || 0)}`
                            }/>

                            <ListItemSecondaryAction>
                                <span style={{display: 'inline-block', marginLeft: 10}}>{ project.faucet && '🚰'}</span>
                                <span style={{display: 'inline-block', marginLeft: 10}}>{ project.locker && '🌋'}</span>
                            </ListItemSecondaryAction>
                        </ListItem>
                    </RouterLink>
                })
            }
        </List>
    )
}